'use client';

import { useState } from 'react';
import { useCart } from '@/context/CartContext';
import { formatPrice } from '@/data/products';
import { useRouter } from 'next/navigation';
import CheckoutForm, { CustomerInfo } from '@/components/CheckoutForm';
import Link from 'next/link';
import Image from 'next/image';

export default function Cart() {
  const { items, removeFromCart, updateQuantity, getTotalPrice, clearCart } = useCart();
  const router = useRouter();
  const [showCheckout, setShowCheckout] = useState(false);

  const handleCheckout = (customerInfo: CustomerInfo) => {
    // Store customer info in localStorage for the confirmation page
    localStorage.setItem('customerInfo', JSON.stringify(customerInfo));
    localStorage.setItem('orderItems', JSON.stringify(items));
    localStorage.setItem('orderTotal', getTotalPrice().toString());
    
    clearCart();
    router.push('/order-confirmation');
  };

  if (items.length === 0) {
    return (
      <div className="min-h-screen bg-yellow-50 py-12">
        <div className="max-w-2xl mx-auto px-4 sm:px-6 lg:max-w-7xl lg:px-8">
          <div className="text-center">
            <div className="bg-white rounded-lg shadow-md p-8">
              <i className="fas fa-shopping-cart text-6xl text-gray-400 mb-4"></i>
              <h1 className="text-2xl font-bold text-gray-800 mb-4">Your Cart is Empty</h1>
              <p className="text-gray-600 mb-8">Add some items to your cart to get started.</p>
              <Link
                href="/"
                className="inline-block bg-gray-800 text-white px-6 py-3 rounded-lg hover:bg-gray-700 transition-colors duration-200 font-bold"
              >
                Continue Shopping
              </Link>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-yellow-50 py-12">
      <div className="max-w-2xl mx-auto px-4 sm:px-6 lg:max-w-7xl lg:px-8">
        <h1 className="text-3xl font-bold text-gray-800 mb-8">Shopping Cart</h1>

        <div className="grid grid-cols-1 gap-8 lg:grid-cols-12">
          <div className="lg:col-span-7">
            <div className="bg-white rounded-lg shadow-md overflow-hidden">
              <div className="p-4 bg-gray-50 border-b">
                <h2 className="text-lg font-bold text-gray-800">Order Items</h2>
              </div>
              <ul className="divide-y divide-gray-200">
                {items.map((item) => (
                  <li key={`${item.id}-${item.selectedSize}`} className="p-6">
                    <div className="flex items-center">
                      <div className="relative h-24 w-24">
                        <Image
                          src={item.image}
                          alt={item.name}
                          fill
                          className="object-cover rounded-lg shadow-sm"
                          sizes="(max-width: 96px) 100vw, 96px"
                        />
                      </div>
                      <div className="ml-6 flex-1">
                        <div className="flex items-center justify-between">
                          <div>
                            <h3 className="text-lg font-bold text-gray-800">{item.name}</h3>
                            <p className="mt-1 text-sm text-gray-600">Size: <span className="font-medium">{item.selectedSize}</span></p>
                          </div>
                          <p className="text-lg font-bold text-gray-800">
                            {formatPrice(item.price * item.quantity)}
                          </p>
                        </div>
                        <div className="mt-4 flex items-center justify-between">
                          <div className="flex items-center space-x-4 bg-gray-50 rounded-lg p-2">
                            <button
                              onClick={() => updateQuantity(item.id, item.selectedSize, item.quantity - 1)}
                              className="w-8 h-8 flex items-center justify-center rounded-md bg-white border border-gray-300 text-gray-600 hover:bg-gray-100"
                            >
                              <i className="fas fa-minus text-xs"></i>
                            </button>
                            <span className="text-gray-800 font-bold min-w-[2rem] text-center">{item.quantity}</span>
                            <button
                              onClick={() => updateQuantity(item.id, item.selectedSize, item.quantity + 1)}
                              className="w-8 h-8 flex items-center justify-center rounded-md bg-white border border-gray-300 text-gray-600 hover:bg-gray-100"
                            >
                              <i className="fas fa-plus text-xs"></i>
                            </button>
                          </div>
                          <button
                            onClick={() => removeFromCart(item.id, item.selectedSize)}
                            className="text-red-600 hover:text-red-800 p-2 rounded-md hover:bg-red-50"
                          >
                            <i className="fas fa-trash"></i>
                          </button>
                        </div>
                      </div>
                    </div>
                  </li>
                ))}
              </ul>
            </div>
          </div>

          <div className="lg:col-span-5">
            {!showCheckout ? (
              <div className="bg-white rounded-lg shadow-md p-6">
                <h2 className="text-lg font-bold text-gray-800 mb-4">Order Summary</h2>
                <div className="space-y-4">
                  <div className="flex justify-between">
                    <span className="text-gray-600">Subtotal</span>
                    <span className="text-gray-800 font-medium">{formatPrice(getTotalPrice())}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600">Shipping</span>
                    <span className="text-green-600 font-medium">Free</span>
                  </div>
                  <div className="border-t border-gray-200 pt-4">
                    <div className="flex justify-between">
                      <span className="text-lg font-bold text-gray-800">Total</span>
                      <span className="text-lg font-bold text-gray-800">
                        {formatPrice(getTotalPrice())}
                      </span>
                    </div>
                  </div>
                </div>

                <button
                  onClick={() => setShowCheckout(true)}
                  className="w-full mt-6 bg-gray-800 text-white py-3 px-4 rounded-lg hover:bg-gray-700 transition-colors duration-200 font-bold shadow-md"
                >
                  <i className="fas fa-credit-card mr-2"></i>
                  Proceed to Checkout
                </button>

                <Link
                  href="/"
                  className="block mt-4 text-center text-sm text-gray-600 hover:text-gray-800 font-medium"
                >
                  Continue Shopping
                </Link>
              </div>
            ) : (
              <CheckoutForm onSubmit={handleCheckout} total={getTotalPrice()} />
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
